* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  max-width: 1200px;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.breed-select {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.loader {
  text-align: center;
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  font-size: 18px;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  text-align: center;
  color: red;
  padding: 10px;
  font-size: 18px;
  display: none;
}

.cat-info {
  text-align: center;
  padding: 20px;
  font-size: 18px;
  display: none;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  margin-top: 20px;
}

